import React from 'react'
import Modal from 'react-bootstrap/Modal'
import Button from 'react-bootstrap/Button'

const LoggedOut = () => {

  const signInAgain = (ev) => {
    ev.preventDefault()
    window.location = `${window.location.origin}/api/reqlogin/`
  }
  return (
    <>
      <Modal.Dialog>
        <Modal.Header closeButton>
          <Modal.Title>You have signed out</Modal.Title>
        </Modal.Header>

        <Modal.Body>
          <p>You have signed out. What would you like to do now?</p>
        </Modal.Body>

        <Modal.Footer>
          <Button variant="secondary">Close</Button>
          <Button variant="primary" onClick={signInAgain}>Sign in again</Button>
        </Modal.Footer>
      </Modal.Dialog>
    </>
  )
}

export default LoggedOut
